<template>
  <div class="scenarioIntro">
      <div class="intro">
        <span style="font-size: 30px"><strong>{{scenario.name}}详细信息</strong></span>
        <div><span style="font-size: 20px">{{scenario.intro}}</span></div>
        <div><span style="font-size: 20px">车道类型:{{scenario.drivewayType.type}}</span> </div>
        <div><span style="font-size: 20px">无人车规划任务:{{scenario.taskPlanning.task}}</span></div>
        <div><span style="font-size: 20px">障碍物类型:{{scenario.obstacleType.type}}</span></div>
        <div><span style="font-size: 20px">障碍物数量:{{scenario.num}}</span></div>
        <div><el-button style="background-color:white;width:100px" @click="downloadJson">
          <span style="font-size: 20px;font-weight: bold">下载</span></el-button></div>
       </div>
       <div class="video-wrapper">
        <video controls width="100%" :src="scenario.video" type="video/mp4"></video>
       </div>
  </div>
</template>

<script>
  export default {
    name: 'ScenarioIntro',
    data () {
      return {
        scenario: ''
      }
    },
    mounted () {
      this.loadScenario()
    },
    methods: {
      loadScenario () {
        var _this = this
        this.$axios.get('/scenarios/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.scenario = resp.data.result
          // console.log(_this.scenario)
          }
        })
      },
      downloadJson () {
        let _this = this
        this.$axios.get('/scenarios/download/' + this.scenario.id, {
          responseType: 'blob'
        }).then(resp => {
          const blob = new Blob([resp.data], {
            type: 'application/json'
          })
          const link = document.createElement('a')
          link.href = window.URL.createObjectURL(blob) // 指定下载链接
          link.setAttribute('download', _this.scenario.fileName) // 指定下载文件名
          document.body.appendChild(link)
          link.click()
          window.URL.revokeObjectURL(link.href) // 释放URL对象
        })
      }
    }
  }
</script>

<style scoped>
.scenarioIntro {
  padding: 40px; /* 页边距 */
  display: flex;
  height: 100vh; /* 占满整个屏幕 */
  background-color: #f5f9ff; /* 背景色 */
}
.video-wrapper {
  position: relative;
  width: 80%;
  padding-top: 56.25%; /* 16:9 比例 */
  margin-top: 30px; /* 视频和 intro 之间的间距 */
  padding: 40px;
}
video {
  position: absolute;
  top: 0;
  left: 0;
}
.intro {
  padding: 40px;
}
</style>



